<script>
import {
  BCard,
  BButton,
  BFormGroup,
  BForm,
  BFormInput,
  BFormRow,
  BSpinner,
  BFormCheckbox,
  BAvatar,
} from 'bootstrap-vue'


/** import axios */
import { getOnlineIncome, getOnlineIncomeDetail, verifyOnlineIncome } from '@/api/costsServices'
/** import toast */
import ToastificationContent from "@/@core/components/toastification/ToastificationContent.vue";

export default {
  data() {
    return {
      loading: false,
      loadingOnlineIncomeDetail: false,
      prepaid: [],
      onlineIncomeDetail: [],
      selected: [],
      online_income_id: null
    }
  },
  components: {
    BCard,
    BSpinner,
    BAvatar,
    BButton,
    BCard,
    BFormGroup,
    BForm,
    BFormInput,
    BFormRow,
    BFormCheckbox,
    ToastificationContent,
  },
  methods: {
    /** get data when load page*/
    async getData() {
      try {
        let req = await getOnlineIncome({
          params: {
            'search': null
          }
        })
        /** add feild openDropdownIitem */
        this.prepaid = req.data.list.map((item) => {
          item.openDropdownIitem = false
          return item
        })
      } catch (error) {
        console.error(error);
      }
    },
    /** get details  Online-income
     * @param id online_income_id
    */
    async getDetailOnlineIncome(id) {
      try {
        this.loadingOnlineIncomeDetail = true,
          this.online_income_id = id

        /** show current details and hide befor detail  */
        this.prepaid = this.prepaid.map((item) => {
          if (item.online_income_id == id) {
            item.openDropdownIitem = !item.openDropdownIitem
            return item
          }
          item.openDropdownIitem = false
          return item
        })

        this.onlineIncomeDetail = []
        let req = await getOnlineIncomeDetail({
          params: {
            'online_income_id': id
          }
        })
        /** add feild selected */
        this.onlineIncomeDetail = req.data.list.map((item) => {
          item.selected = false
          return item
        });
      } catch (error) {
        console.error(error);
      }
      this.loadingOnlineIncomeDetail = false

    },
    /** submit btn payment */
    async submitPayment() {
      try {
        if (this.loading) return

        let ts = this
        this.selected = []
        this.onlineIncomeDetail.map((item) => {
          if (item.selected) ts.selected.push({ 'user_unit_debt_id': item.user_unit_debt_id, 'amount': item.debt_share })
        })

        if (!this.selected.length) return this.$toast({
          component: ToastificationContent,
          props: {
            title: "      ",
            icon: 'AlertCircleIcon',
            variant: 'danger',
          },
        })

        this.loading = true;
        let data = {
          "online_income_id": this.online_income_id,
          "verify_list": this.selected
        }

        let req = await verifyOnlineIncome(data)
        this.getData();
        this.$toast({
          component: ToastificationContent,
          props: {
            title: req.data.message,
            icon: 'AlertCircleIcon',
            variant: 'success',
          },
        })
      } catch (error) {
        console.error(error)
      }
      this.loading = false
    }
  },
  async mounted() {
    this.getData();
  }
}
</script>

